const tpl = ()=>(
	`
		<div id='chatter'>
			<ul>
				<li id='search'>
					<input type='text' placeholder=''><div class='seach-icon'></div>
				</li>
				<li class='recent_list'>
					<div>
						<img :src='photo'>
					</div>
					<article>
						<p style="padding-top:5px">
							<span></span>
							<span>20:20</span>
						</p>
						<p style="padding-bottom:5px">
							<span>this is recent</span>
							<span>flag</span>
						</p>
					</article>
				</li>
			</ul>
			<div id='chat'>
				<p id='header'>
					<span>nickname</span>&emsp;
					<span class='down-icon'></span>
				</p>
				<div id='chat_box'>
					<div class='self_talk talk'>
						<div class='language'>,,</div>
						<div class='figure'>
							<img  :src='photo'>
						</div>
					</div>
					<div class='other_talk talk'>
						<div class='figure'>
							<img  :src='photo'>
						</div>
						<div class='language'>,,,,</div>
					</div>

					<div class='self_talk talk'>
						<div class='language'>,,</div>
						<div class='figure'>
							<img  :src='photo'>
						</div>
					</div>
					<div class='other_talk talk'>
						<div class='figure'>
							<img  :src='photo'>
						</div>
						<div class='language'>,,,,</div>
					</div>
					<div class='other_talk talk'>
						<div class='figure'>
							<img  :src='photo'>
						</div>
						<div class='language'>,,,,</div>
					</div>
					<div class='self_talk talk'>
						<div class='language'>,,</div>
						<div class='figure'>
							<img  :src='photo'>
						</div>
					</div>
				</div>
				<div id='footer'>
					<div id='toolbar'>
						<span></span>
						<span></span>
						<span></span>
					</div>
					<textarea id='input_box'></textarea>
					<p id='send'>
						Ctrl+Enter&emsp;<button type='button' id='post'></button>
					</p>
				</div>
			</div>
		</div>
	`
)

export const Chatter = {
	template:tpl(),
	data:()=>({
		photo:'/img/photo.jpg'
	}),
	computed:{

	},
	methods:{

	}
}



